.container {
    width: 1200px;
    margin: 0 auto;
}

header {
    background-color: white;
}

.list {
    height: 41px;

    .list-nav {
        height: 41px;
        line-height: 41px;
    }

    h1 {
        font-size: 14px;
        color: #757575;
        font-weight: 400;

        a {
            font-size: 14px;
            color: #757575;
        }
    }
}

.list-title {
    height: 84px;
    background-color: white;
    width: 100%;
}

.list-box {
    ul {
        height: 30px;
        margin: 20px 30px;

        li {
            float: left;
            width: 88px;
            height: 20px;
            line-height: 20px;
            color: rgb(66, 66, 66);
            font-size: 14px;
            text-align: center;
            font-weight: 400;
        }
    }
}

.list-inner {

    .items {
        float: left;
        width: 23%;
        height: 430px;
        margin-right: 14px;
        margin-bottom: 14px;
        background-color: white;
        position: relative;
        &:hover {
            box-shadow: 0 15px 30px rgb(0 0 0 / 10%);
            transform: translate3d(0, -2px, 0);
            transition: .5s;
        }

        a {
            display: block;
            padding-top: 47px;
            text-align: center;
            color: #757575;

            .item-img {
                width: 200px;
                height: 200px;
                margin: 0 auto;

                img {
                    width: 100%;
                    height: 100%;
                }
            }
            .item-name {
                margin: 0 auto;
                width: 100%;
                font-size: 14px;
                font-weight: 400;
                text-overflow: ellipsis;
                white-space: nowrap;
                overflow: hidden;
                margin-top: 20px;
            }
            .item-price{
                width: 100%;
                margin-bottom: 15px;
                color: #ff6700;
            }
            .small-img{
                width: 34px;
                height: 34px;
                border: 1px solid #ff6700;
                margin: 0 auto;
                img{
                    width: 100%;
                    height: 100%;
                }
            }
        }


    }
}